import { Container, Row, Col, Image } from 'react-bootstrap';
import React, { Component } from 'react';
import Navbar from './Navbar';
import { Link } from "react-router-dom";
import Countdown, {zeroPad} from 'react-countdown';
import { loadBlockchainData, loadInGameCoin, loadWeb3 } from './loadweb3.js';
import no from './images/no.png';
import pCard1 from './images/powerCard/pCard1.png';
import pCard2 from './images/powerCard/pCard2.png';
import pCard3 from './images/powerCard/pCard3.png';

import mCard1 from './images/mineCard/1.png';
import mCard2 from './images/mineCard/2.png';
import mCard3 from './images/mineCard/3.png';

class Craft extends Component {

  async componentWillMount() {
    const loadBlock = await loadBlockchainData(this.props.dispatch);
    await this.setState(loadBlock);
    if(typeof loadBlock.account !=='undefined'){
      const coinToken = await loadInGameCoin();
      this.setState(coinToken);

      await this.loadAllCard();
    }
  }

  async connectWallet(){
    await loadWeb3();
    const loadBlock = await loadBlockchainData(this.props.dispatch);
    await this.setState(loadBlock);
  }

  async reloadCoinToken(e){
    const coinToken = await loadInGameCoin();
    this.setState(coinToken);
  }

  async craft( cardId, mecType, craftcost){
    console.log(cardId);
    console.log(mecType);
    console.log(craftcost);
    const _amount = parseInt(craftcost) * 10**18;
    if (mecType === "0") {
      //_amount = parseInt(craftcost) * 10**18;
    }

    const craftingIndex = await this.state.mineABI.methods.craft( cardId ).send({value: _amount.toString(),from: this.state.account});
    console.log(craftingIndex);
  }

  async loadAllCard(e){
    console.log("loadAllCard");
    let myItems = [];
    //const itemsIndex = await this.state.mineABI.methods._cardAttr(1).call({from: this.state.account});
    //console.log(itemsIndex);
    
      for (let i = 1; i <= 6; i++) {
        //Call แบบไม่ต้องไปที่ฟังชั่น
        let itemsDetail = await this.state.CardABI.methods._cardAttr( i ).call({from: this.state.account});
        let CardImg;
        if (itemsDetail.mecType === "0") {
          if (itemsDetail.rarity === "1") {
            CardImg = mCard1;
          }else if (itemsDetail.rarity === "2") {
            CardImg = mCard2;
          }else if (itemsDetail.rarity === "3") {
            CardImg = mCard3;
          }
        }else{
          if (itemsDetail.rarity === "1") {
            CardImg = pCard1;
          }else if (itemsDetail.rarity === "2") {
            CardImg = pCard2;
          }else if (itemsDetail.rarity === "3") {
            CardImg = pCard3;
          }
        }
        let cardImg = [];
        if (itemsDetail.mecType === "0") {
          cardImg.push(<Image src={ CardImg }  style={{height:"250px"}}/>);
        }else{cardImg.push(<Image src={ CardImg }  style={{height:"250px"}}/>);}

        myItems.push(<>
            <Col sm={6}>
              <Row className="relative pA20">
                <Col className="text-center">
                  {cardImg}
                </Col>
                <Col>
                  <div><span>Name</span></div>
                  <div><span>Rarity: {itemsDetail.rarity}</span></div>
                  <div><span>Reward: {this.state.web3.utils.fromWei(itemsDetail.rewardRate)}</span></div>
                  <div><span>Energy Consumed: {itemsDetail.energyCon}</span></div>
                  <div><span>Durability Consumed: {itemsDetail.durabilityCon}</span></div>
                  <div className="f24"><b>{itemsDetail.durability}/{itemsDetail.durability}</b></div>
                  <div className="f18 pT10">
                    <button className="button-71" onClick={(e) => this.craft( i, itemsDetail.mecType, itemsDetail.craftCost )}>Craft</button>
                  </div>
                  
                </Col>
              </Row>
            </Col>
            </>);
        
      }
    
    
    //
    await this.setState({myItems:myItems});
    
  }


  
  constructor(props) {
    super(props)
    this.state = {
      web3: 'undefined',
      account: '',
      mineABI: null,
      myItems:[],
    }
    this.reloadCoinToken = this.reloadCoinToken.bind(this);
  }

//
  render() {
    return (<>
      
      { this.state.showModalContent }

      <Navbar isConnect={this.state.isConnect} loadWeb3={this.loadWeb3} connectWallet={this.connectWallet} account={this.state.account} CurrencyABI={this.state.CurrencyABI} mCoin={this.state.mCoin} pCoin={this.state.pCoin} energy={this.state.energy} mineABI={this.state.mineABI} reloadCoinToken={this.reloadCoinToken}/>

      <Container fluid>
        <Row className="pT10">
          <Col sm={12} className="leftStatus" style={{maxHeight:"220px"}} >
            <Row className="pB10">
              <Col><Image src={ no } width="12px"/></Col>
              <Col className="text-right"><Image src={ no } width="12px"/></Col>
            </Row>

            <Row className="text-center">
              <Col><span className="f32 labelPage">CARFT</span></Col>
            </Row>

            <Row className="pT10">
              <Col><Image src={ no } width="12px"/></Col>
              <Col className="text-right"><Image src={ no } width="12px"/></Col>
            </Row>
          </Col>
        </Row>

        <Row className="mt-2" >
          {this.state.myItems}
        </Row>

      </Container>
    </>);
  }
}

export default Craft;
