import { Container, Row, Col, Image, Button } from 'react-bootstrap';
import React, { Component } from 'react';
import Navbar from './Navbar';
import { Link } from "react-router-dom";
import Countdown, {zeroPad} from 'react-countdown';
import { loadBlockchainData, loadInGameCoin, loadWeb3 } from './loadweb3.js';
import no from './images/no.png';

import pCard1 from './images/powerCard/pCard1.png';
import pCard2 from './images/powerCard/pCard2.png';
import pCard3 from './images/powerCard/pCard3.png';

import mCard1 from './images/mineCard/1.png';
import mCard2 from './images/mineCard/2.png';
import mCard3 from './images/mineCard/3.png';

class Mycharacter extends Component {

  async componentWillMount() {
    const loadBlock = await loadBlockchainData(this.props.dispatch);
    await this.setState(loadBlock);
    if(typeof loadBlock.account !=='undefined'){
      const coinToken = await loadInGameCoin();
      this.setState(coinToken);

      await this.loadMachine();
    }
  }

  async connectWallet(){
    await loadWeb3();
    const loadBlock = await loadBlockchainData(this.props.dispatch);
    await this.setState(loadBlock);
  }

  async reloadCoinToken(e){
    const coinToken = await loadInGameCoin();
    this.setState(coinToken);
  }

  async unInstallCard(indexId){
    const installIndex = await this.state.mineABI.methods.unStakingCard( indexId ).send({from: this.state.account});
    this.loadMachine();
  }

  async mining(indexId, miningType, energyCon){
    console.log(energyCon);
    let useEnergy = parseInt(energyCon);
    if (miningType === 1) {
      useEnergy = energyCon*24;
    }
    if (useEnergy > this.state.energy) {
      alert("Energy not enough!!!");
      return;
    }
    const miningIndex = await this.state.mineABI.methods.mining( indexId, miningType ).send({from: this.state.account});
    console.log(miningIndex);
    await this.loadMachine();
    this.reloadCoinToken();
  }

  async claim(indexId){
    console.log(indexId);
    const installIndex = await this.state.mineABI.methods.claim( indexId ).send({from: this.state.account});
    this.loadMachine();
    this.reloadCoinToken();
  }

  async repair(indexId,durabilityMax,durabilityCur){
    const mCoinUse = (durabilityMax - durabilityCur)/5;//maxhp - currenthp
    if (mCoinUse > this.state.mCoin) {
      alert("Money not enough!!!");
      return;
    }
    const repairIndex = await this.state.mineABI.methods.repairCard( indexId ).send({from: this.state.account});
    this.loadMachine();
    this.reloadCoinToken();
  }

  async upgrade(){
    let _amount = 1 * 10**17;
    const upgradeStakeCard = await this.state.mineABI.methods.upgradeStakeCard().send({value: _amount.toString(),from: this.state.account});
    this.loadMachine();
  }

  async loadMachine(e){
    let myItems = [];
    let currentMachineStake = 0;
    let currentResourceStake = 0;
    let maxMachineStake = 2;
    let maxResourceStake = 2;

    const renderer = ({ hours, minutes, seconds, completed }) => {
      if (completed) {
        return <div className="f18 pT10"><button className="button-71 buttonMining" onClick={(e) => this.mining(0)}>Claim</button></div>;
      } else {
        return <span>{zeroPad(hours)}:{zeroPad(minutes)}:{zeroPad(seconds)}</span>;
      }
    };

    const itemsStaking = await this.state.mineABI.methods.getStakingCard( this.state.account ).call({from: this.state.account});
    console.log(itemsStaking);
    
    currentMachineStake = itemsStaking.indexMachineId.length;
    currentResourceStake = itemsStaking.indexResourceId.length;

    if (itemsStaking.maxMachineStake !== "0") {
      maxMachineStake = parseInt(itemsStaking.maxMachineStake) + 2;
    }

    if (itemsStaking.maxMachineStake !== "0") {
      maxMachineStake = parseInt(itemsStaking.maxMachineStake) + 2;
    }

    
    if (currentMachineStake > 0) {
      for (let i = 0; i < currentMachineStake; i++) {
        //Call แบบไม่ต้องไปที่ฟังชั่น
        let itemsStatus = await this.state.mineABI.methods._mineDetails( itemsStaking.indexMachineId[i] ).call({from: this.state.account});
        //console.log(itemsStatus);
        let itemsDetail = await this.state.CardABI.methods._cardAttr( itemsStatus.cardId ).call({from: this.state.account});
          //console.log(itemsDetail);
        let mineCard;
        if (itemsDetail.rarity === "1") {
          mineCard = mCard1;
        }else if (itemsDetail.rarity === "2") {
          mineCard = mCard2;
        }else if (itemsDetail.rarity === "3") {
          mineCard = mCard3;
        }

        let powerCard;
        if (itemsDetail.resRarity === "4") {
          powerCard = pCard1;
        }else if (itemsDetail.resRarity === "5") {
          powerCard = pCard2;
        }else if (itemsDetail.resRarity === "6") {
          powerCard = pCard3;
        }

        let cardImg = [];
        if (itemsDetail.mecType === "0") {
          cardImg.push(<Image src={ mineCard }  style={{height:"250px"}}/>);
        }else{
          cardImg.push(<Image src={ powerCard }  style={{height:"250px"}}/>);
        }
        ///

        const cardReward = this.state.web3.utils.fromWei(itemsDetail.rewardRate);

        if (itemsStatus.miningTimeEnd*1000 <= Date.now()) {
          myItems.push(<>
              <Col sm={6}>
                <Row className="relative cardStatus mb-50">
                  <Col sm={12} className="text-right"><Image src={ no } width="10px"/></Col>
                  <Col sm={5} className="text-center">
                    {cardImg}
                  </Col>
                  <Col sm={7} className="pT10">
                    <Row>
                      <Col><span>Reward:</span></Col>
                      <Col className="text-right f18"><b>{cardReward}</b></Col>
                    </Row>
                    <Row>
                      <Col sm="auto"><span>Energy Consumed: </span></Col>
                      <Col className="text-right f18"><b>{itemsDetail.energyCon}</b></Col>
                    </Row>
                    <Row>
                      <Col sm="auto"><span>Durability Consumed:  </span></Col>
                      <Col className="text-right f18"><b>{itemsDetail.durabilityCon}</b></Col>
                    </Row>
                    <Row>
                      <Col sm="auto"><span>Durability:  </span></Col>
                      <Col className="text-right f18"><b>{itemsStatus.durabilityCur}/{itemsDetail.durability}</b></Col>
                    </Row>
                    <Row className="pT10">
                      <Col  className="pA0 text-center">
                        <button className="button-71 buttonMining" style={{width:"90%"}} onClick={(e) => this.mining(itemsStaking.indexMachineId[i],0, itemsDetail.energyCon)}>MINE 1 Hr.</button>
                      </Col>
                      <Col  className="pA0 text-center pL5">
                        <button className="button-71 buttonMining" style={{width:"90%"}} onClick={(e) => this.mining(itemsStaking.indexMachineId[i],1, itemsDetail.energyCon)}>MINE 6 Hr.</button>
                      </Col>
                    </Row>
                    <Row className="pT10">
                      <Col className="pA0 text-center">
                        <button className="button-71 buttonMining" style={{width:"90%"}} onClick={(e) => this.repair(itemsStaking.indexMachineId[i], itemsDetail.durability, itemsStatus.durabilityCur )}>Repair</button>
                      </Col>
                      <Col  className="pA0 text-center pL5">
                        <button className="button-71 buttonMining mL5" style={{width:"90%"}} onClick={(e) => this.unInstallCard(itemsStaking.indexMachineId[i])}>Uninstall</button>
                      </Col>
                    </Row>
                  </Col>
                  <Col sm={12}><Image src={ no } width="10px"/></Col>
                </Row>
              </Col>
              </>);
        }else {

          myItems.push(<>
              <Col sm={6}>
                <Row className="relative cardStatus mb-50">
                  <Col sm={12} className="text-right"><Image src={ no } width="10px"/></Col>
                  <Col className="text-center">
                    {cardImg}
                  </Col>
                  <Col sm={7} className="pT10">
                    <Row>
                      <Col><span>Reward:</span></Col>
                      <Col className="text-right f18"><b>{cardReward}</b></Col>
                    </Row>
                    <Row>
                      <Col sm="auto"><span>Energy Consumed: </span></Col>
                      <Col className="text-right f18"><b>{itemsDetail.energyCon}</b></Col>
                    </Row>
                    <Row>
                      <Col sm="auto"><span>Durability Consumed:  </span></Col>
                      <Col className="text-right f18"><b>{itemsDetail.durabilityCon}</b></Col>
                    </Row>
                    <Row>
                      <Col sm="auto"><span>Durability:  </span></Col>
                      <Col className="text-right f18"><b>{itemsStatus.durabilityCur}/{itemsDetail.durability}</b></Col>
                    </Row>
                    <Countdown date={ itemsStatus.miningTimeEnd*1000 }>
                      <div>
                        <Row className="pT10">
                          <Col  className="pA0 text-center">
                            <button className="button-71 buttonMining" style={{width:"90%"}} onClick={(e) => this.mining(itemsStaking.indexMachineId[i],0, itemsDetail.energyCon)}>MINE 1 Hr.</button>
                          </Col>
                          <Col  className="pA0 text-center pL5">
                            <button className="button-71 buttonMining" style={{width:"90%"}} onClick={(e) => this.mining(itemsStaking.indexMachineId[i],1, itemsDetail.energyCon)}>MINE 6 Hr.</button>
                          </Col>
                        </Row>
                        <Row className="pT10">
                          <Col className="pA0 text-center">
                            <button className="button-71 buttonMining" style={{width:"90%"}} onClick={(e) => this.repair(itemsStaking.indexMachineId[i], itemsDetail.durability, itemsStatus.durabilityCur )}>Repair</button>
                          </Col>
                          <Col  className="pA0 text-center pL5">
                            <button className="button-71 buttonMining mL5" style={{width:"90%"}} onClick={(e) => this.unInstallCard(itemsStaking.indexMachineId[i])}>Uninstall</button>
                          </Col>
                        </Row>
                      </div>
                    </Countdown>
                  </Col>

                  <Col sm={12}><Image src={ no } width="10px"/></Col>
                </Row>
              </Col>
              </>);
        }
      }
    }

    if (currentResourceStake > 0) {
      
      
      for (let i = 0; i < currentResourceStake; i++) {
        //Call แบบไม่ต้องไปที่ฟังชั่น
        let itemsStatus = await this.state.mineABI.methods._mineDetails( itemsStaking.indexResourceId[i] ).call({from: this.state.account});
        //console.log(itemsStatus);
        let itemsDetail = await this.state.CardABI.methods._cardAttr( itemsStatus.cardId ).call({from: this.state.account});
          console.log(itemsDetail);
        let powerCard;
        if (itemsDetail.rarity === "1") {
          powerCard = pCard1;
        }else if (itemsDetail.rarity === "2") {
          powerCard = pCard2;
        }else if (itemsDetail.rarity === "3") {
          powerCard = pCard3;
        }

        let cardImg = [];
        cardImg.push(<Image src={ powerCard }  style={{height:"250px"}}/>);
//
        const cardReward = this.state.web3.utils.fromWei(itemsDetail.rewardRate);

        if (itemsStatus.miningTimeEnd*1000 <= Date.now()) {
          myItems.push(<>
              <Col sm={6} className="">
                <Row className="relative cardStatus mb-50">
                  <Col sm={12} className="text-right"><Image src={ no } width="10px"/></Col>
                  <Col sm={5} className="text-center">
                    {cardImg}
                  </Col>
                  <Col sm={7} className="pT10">
                    <Row>
                      <Col><span>Reward:</span></Col>
                      <Col className="text-right f18"><b>{cardReward}</b></Col>
                    </Row>
                    <Row>
                      <Col sm="auto"><span>Energy Consumed: </span></Col>
                      <Col className="text-right f18"><b>{itemsDetail.energyCon}</b></Col>
                    </Row>
                    <Row>
                      <Col sm="auto"><span>Durability Consumed:  </span></Col>
                      <Col className="text-right f18"><b>{itemsDetail.durabilityCon}</b></Col>
                    </Row>
                    <Row>
                      <Col sm="auto"><span>Durability:  </span></Col>
                      <Col className="text-right f18"><b>{itemsStatus.durabilityCur}/{itemsDetail.durability}</b></Col>
                    </Row>
                    <Row className="pT10">
                      <Col  className="pA0 text-center">
                        <button className="button-71 buttonMining" style={{width:"90%"}} onClick={(e) => this.mining(itemsStaking.indexResourceId[i],0, itemsDetail.energyCon)}>MINE 1 Hr.</button>
                      </Col>
                      <Col  className="pA0 text-center pL5">
                        <button className="button-71 buttonMining" style={{width:"90%"}} onClick={(e) => this.mining(itemsStaking.indexResourceId[i],1, itemsDetail.energyCon)}>MINE 6 Hr.</button>
                      </Col>
                    </Row>
                    <Row className="pT10">
                      <Col className="pA0 text-center">
                        <button className="button-71 buttonMining" style={{width:"90%"}} onClick={(e) => this.repair(itemsStaking.indexResourceId[i], itemsDetail.durability, itemsStatus.durabilityCur )}>Repair</button>
                      </Col>
                      <Col  className="pA0 text-center pL5">
                        <button className="button-71 buttonMining mL5" style={{width:"90%"}} onClick={(e) => this.unInstallCard(itemsStaking.indexResourceId[i])}>Uninstall</button>
                      </Col>
                    </Row>
                    
                  </Col>
                  <Col sm={12}><Image src={ no } width="10px"/></Col>
                </Row>
              </Col>
              </>);
        }else {

          myItems.push(<>
              <Col sm={6}>
                <Row className="relative cardStatus mb-50">
                  <Col sm={12} className="text-right"><Image src={ no } width="10px"/></Col>
                  <Col className="text-center">
                    {cardImg}
                  </Col>

                  <Col sm={7} className="pT10">
                    <Row>
                      <Col><span>Reward:</span></Col>
                      <Col className="text-right f18"><b>{cardReward}</b></Col>
                    </Row>
                    <Row>
                      <Col sm="auto"><span>Energy Consumed: </span></Col>
                      <Col className="text-right f18"><b>{itemsDetail.energyCon}</b></Col>
                    </Row>
                    <Row>
                      <Col sm="auto"><span>Durability Consumed:  </span></Col>
                      <Col className="text-right f18"><b>{itemsDetail.durabilityCon}</b></Col>
                    </Row>
                    <Row>
                      <Col sm="auto"><span>Durability:  </span></Col>
                      <Col className="text-right f18"><b>{itemsStatus.durabilityCur}/{itemsDetail.durability}</b></Col>
                    </Row>
                    <Countdown date={ itemsStatus.miningTimeEnd*1000 }>
                    <div>
                      <Row className="pT10">
                        <Col  className="pA0 text-center">
                          <button className="button-71 buttonMining" style={{width:"90%"}} onClick={(e) => this.mining(itemsStaking.indexResourceId[i],0, itemsDetail.energyCon)}>MINE 1 Hr.</button>
                        </Col>
                        <Col  className="pA0 text-center pL5">
                          <button className="button-71 buttonMining" style={{width:"90%"}} onClick={(e) => this.mining(itemsStaking.indexResourceId[i],1, itemsDetail.energyCon)}>MINE 6 Hr.</button>
                        </Col>
                      </Row>
                      <Row className="pT10">
                        <Col className="pA0 text-center">
                          <button className="button-71 buttonMining" style={{width:"90%"}} onClick={(e) => this.repair(itemsStaking.indexResourceId[i], itemsDetail.durability, itemsStatus.durabilityCur )}>Repair</button>
                        </Col>
                        <Col  className="pA0 text-center pL5">
                          <button className="button-71 buttonMining mL5" style={{width:"90%"}} onClick={(e) => this.unInstallCard(itemsStaking.indexResourceId[i])}>Uninstall</button>
                        </Col>
                      </Row>
                    </div>
                    </Countdown>
                  </Col>
                  <Col sm={12}><Image src={ no } width="10px"/></Col>
                </Row>
              </Col>
              </>);
        }
      }
    }
    
    //
    await this.setState({myItems:myItems,maxMachineStake:maxMachineStake,maxResourceStake:maxResourceStake,currentMachineStake:currentMachineStake,currentResourceStake:currentResourceStake});
  }

  
  constructor(props) {
    super(props)
    this.state = {
      web3: 'undefined',
      account: '',
      mineABI: null,
      myItems:[],
      currentMachineStake:0,
      currentResourceStake:0,
      maxMachineStake:2,
      maxResourceStake:2,
    }
    this.reloadCoinToken = this.reloadCoinToken.bind(this);
  }

//
  render() {
    return (<>
      
      { this.state.showModalContent }

      <Navbar isConnect={this.state.isConnect} loadWeb3={this.loadWeb3} connectWallet={this.connectWallet} account={this.state.account} CurrencyABI={this.state.CurrencyABI} mCoin={this.state.mCoin} pCoin={this.state.pCoin} energy={this.state.energy} mineABI={this.state.mineABI} reloadCoinToken={this.reloadCoinToken}/>

      <Container fluid>
        <Row className="mt-5">
          <Col sm={3} className="leftStatus mL10" style={{maxHeight:"220px"}}>
            <Row className="pB10">
              <Col><Image src={ no } width="12px"/></Col>
              <Col className="text-right"><Image src={ no } width="12px"/></Col>
            </Row>

            <Row className="text-center">
              <Col><span className="f32 labelPage">MINE STATUS</span></Col>
            </Row>

            <Row className="mT10 text-center fadeBg pT10 pB5">
              <Col>
                <span className="f18 labelPage">Machine Max</span>
              </Col>
              <Col>
                <span className="orange">{this.state.currentMachineStake}/{this.state.maxMachineStake}</span>
              </Col>
                {/*<button className="button-71"  onClick={(e) => this.upgrade()}>Upgrade</button>*/}
            </Row>
            <Row className="text-center fadeBg pT10 pB5">
              <Col>
                <span className="f18 labelPage">Resource Max </span>
              </Col>
              <Col>
                <span className="orange">{this.state.currentResourceStake}/{this.state.maxResourceStake}</span>
                {/*<button className="button-71"  onClick={(e) => this.upgrade()}>Upgrade</button>*/}
              </Col>
            </Row>

            <Row className="pT10">
              <Col><Image src={ no } width="12px"/></Col>
              <Col className="text-right"><Image src={ no } width="12px"/></Col>
            </Row>
          </Col>

          
          <Col sm={8} className="mL40">
            <Row>
              {this.state.myItems}
            </Row>
          </Col>

        </Row>

      </Container>
    </>);
  }
}

export default Mycharacter;
