import React, { Component } from 'react';
import { Button, Navbar, Container, Nav, Col, Row, Image } from 'react-bootstrap';
import { Link } from "react-router-dom";
import Web3 from 'web3';
import no from './images/no.png';

class HeaderBar extends Component {

  showModal = () => {
    this.setState({ showModal: true });
  };

  hideModal = () => {
    this.setState({ showModal: false, showModalContent : [] });
  };

  handleInputChange(value) {
    this.setState({ pCoinRe: value });
  }

  async refillEnergyModal(){
    var Content = [];
    Content.push(<>
        <div className="Modal" /*onClick={(e) => this.hideModal(e)}*/>
            <div className="adventureModal pA20 white" >

              <Row className="ab_Top">
                <Col><Image src={ no } width="12px"/></Col>
                <Col className="text-right"><Image src={ no } width="12px"/></Col>
              </Row>

              <Row className="mb10">
                <Col className="text-center close f24">
                  <a href="#" onClick={(e) => this.hideModal(e)} >X CLOSE</a>
                </Col>
              </Row>

              <Row className="mT10 space-center pT10">
                <Col sm="auto" className="text-center">
                  <div className="pT10"><span>1 Power = 5 Energy</span></div>
                  <div>
                    <span>Power: </span>
                    <input type="number" id="quantity" name="quantity" min="1" max="20" style={{width:"200px"}} onChange={ e => this.handleInputChange(e.target.value) } />
                  </div>
                  <div className="pT10">
                    <button className="button-71 buttonMining" variant="primary" onClick={(e) => this.refillEnergy()}>Exchange</button>
                  </div>
                  <div className="mT10">
                  { this.state.checkApprove }
                  </div>
                </Col>
              </Row>

              <Row className="pT10 ab_bottom">
                  <Col><Image src={ no } width="12px"/></Col>
                  <Col className="text-right"><Image src={ no } width="12px"/></Col>
                </Row>
            </div >
        </div >
    </>);

    this.setState({showModalContent:Content});
  };
  ///

  async refillEnergy(pCoin){
    const web3 = new Web3(window.ethereum);
    const pCoinDe = web3.utils.toWei( this.state.pCoinRe.toString() );
    const installIndex = await this.props.CurrencyABI.methods.refillEnergy( pCoinDe ).send({from: this.props.account});
    console.log(installIndex);
    this.props.reloadCoinToken();
    this.hideModal();
  }

  constructor(props) {
    super(props)
    this.state = {
      showModalContent: [],
      pCoin:0,
      energy:0,
      pCoinRe:0,
    }
  }

  render() {
    let connectButton;
    if (!this.props.isConnect) {
      connectButton = <Button variant="outline-primary" size="sm" onClick={this.props.connectWallet}>
          <span className="white" >connect wallet</span>
        </Button>
    }else{
      connectButton = <Col className="walletAddress"><span>ACC: {this.props.account.slice(0, 8) + (this.props.account.length > 8 ? "..." : "")}</span></Col>
    }
    return (<>
      { this.state.showModalContent }

            
      <Row style={{justifyContent:"flex-end"}}>
        <Col sm={8} className="menuAcc">
          <Row className="justCEnd">
            <Col sm="auto" className="navItem orange">Money : {this.props.mCoin}</Col>
            <Col sm="auto" className="navItem orange">Power : {this.props.pCoin}</Col>
            <Col sm="auto" className="navItem orange">Energy : {this.props.energy}<a href="#" onClick={(e) => this.refillEnergyModal()}>+</a></Col>
            <Col sm="auto" className="navItem">{connectButton}</Col>
          </Row>
        </Col>
      </Row>

      <Row style={{justifyContent:"flex-end"}}>
        <Col sm={8} className="menuBg">
          <Row>
            <Col className="navItem"><Link to="/">Home</Link></Col>
            <Col className="navItem"><Link to="/inventory">Inventory</Link></Col>
            <Col className="navItem"><Link to="/mine">Mine</Link></Col>
            <Col className="navItem"><Link to="/craft">Craft</Link></Col>
            <Col className="navItem"><Link to="/market">Market</Link></Col>
            <Col className="navItem"><Link to="/exchange">Exchange</Link></Col>
            <Col className="navItem"></Col>
          </Row>
        </Col>
      </Row>
    </>
    );
  }
}

export default HeaderBar;
