
import React, { Component } from 'react';
import Web3 from 'web3';
import MineContact from './abis/Mine.json';
import MarketContact from './abis/Market.json';
import CardContact from './abis/Card.json';
import CurrencyContact from './abis/Currency.json';
import MMCTokenContact from './abis/MMCToken.json';
import ENCTokenContact from './abis/ENCToken.json';

  var mineABI;
  var CurrencyABI;
  var CardABI;
  var MarketABI;
  var MMCTokenABI;
  var ENCTokenABI;

  var account;
  var MineAddress;
  var MarketAddress;
  var CardAddress;
  var CurrencyAddress;

  export async function loadBlockchainData(dispatch) {
    if(typeof window.ethereum!=='undefined'){
      window.ethereum.on("accountsChanged", (accounts: string[]) => {
        window.location.reload();
      });
      // Subscribe to chainId change
      window.ethereum.on("chainChanged", (chainId: number) => {
        window.location.reload();
      });

      // Subscribe to provider disconnection
      window.ethereum.on("disconnect", (error: { code: number; message: string }) => {
        console.log(error);
      });

      const web3 = new Web3(window.ethereum);
      const netId = await web3.eth.net.getId();
      const accounts = await web3.eth.getAccounts();
      account = accounts[0];
      //load user account balance
      if(typeof accounts[0] !=='undefined'){
        const balance = await web3.eth.getBalance(accounts[0]);
        const accountBalance =  web3.utils.fromWei(balance);
        const isConnect = true;

        mineABI = new web3.eth.Contract(MineContact.abi, MineContact.networks[netId].address);
        CurrencyABI = new web3.eth.Contract(CurrencyContact.abi, CurrencyContact.networks[netId].address);
        CardABI = new web3.eth.Contract(CardContact.abi, CardContact.networks[netId].address);
        MarketABI = new web3.eth.Contract(MarketContact.abi, MarketContact.networks[netId].address);
        MMCTokenABI = new web3.eth.Contract(MMCTokenContact.abi, MMCTokenContact.networks[netId].address);
        ENCTokenABI = new web3.eth.Contract(ENCTokenContact.abi, ENCTokenContact.networks[netId].address);

        MarketAddress = MarketContact.networks[netId].address;
        MineAddress = MineContact.networks[netId].address;
        CardAddress = CardContact.networks[netId].address;
        CurrencyAddress = CurrencyContact.networks[netId].address;

        //const totalSupplyMMC = await MMCTokenABI.methods.totalSupply().call();
        console.log("loadBlockchainData Done!");

        return ({account: accounts[0], balance: balance, web3: web3, accountBalance:accountBalance, isConnect:isConnect, ENCTokenABI:ENCTokenABI, MMCTokenABI:MMCTokenABI, mineABI: mineABI,CurrencyABI: CurrencyABI, CurrencyAddress:CurrencyAddress, CardABI:CardABI, CardAddress:CardAddress, MarketAddress:MarketAddress, MineAddress:MineAddress, MarketABI:MarketABI, loading: true});
      } else {
        //window.alert('Please login with MetaMask')
        return ({account: accounts[0]});
      }

    } 
  }

  export async function loadInGameCoin(){

    if(typeof account!=='undefined'){

      const web3 = new Web3(window.ethereum);
      let coinDetail = await CurrencyABI.methods._coin( account ).call({from: account});
      const mCoin = web3.utils.fromWei(coinDetail.mCoin);
      const pCoin = web3.utils.fromWei(coinDetail.pCoin);
      const energy = coinDetail.energy;
      return ({mCoin: mCoin, pCoin: pCoin,energy:energy});
    }
  }

  export async function loadWeb3(dispatch) {
    if (window.ethereum) {
      window.ethereum.request({
        method: "wallet_addEthereumChain",
        params: [{chainId: "0x61",
          chainName: "BSCTESTNET",
          rpcUrls: ["https://data-seed-prebsc-1-s1.binance.org:8545"],
          nativeCurrency: {
            name: "BINANCE COIN",
            symbol: "BNB",
            decimals: 18,
          },
          blockExplorerUrls: ["https://testnet.bscscan.com/"],
        }],
      });

      window.web3 = new Web3(window.ethereum)
      await window.ethereum.enable()
    }
    else if (window.web3) {
      window.web3 = new Web3(window.web3.currentProvider)
      console.log('currentProvider');
    }
    else {
      window.alert('Non-Ethereum browser detected. You should consider trying MetaMask!')
    }
  }

 

