import { Container, Row, Col, Image, Button } from 'react-bootstrap';
import React, { Component } from 'react';
import Navbar from './Navbar';
import { Link } from "react-router-dom";
import { loadBlockchainData, loadInGameCoin } from './loadweb3.js';

class MyExchange extends Component {

  constructor(props) {
    super(props)
    this.state = {
      content:[],
      fee:0,
    }
    this.handleInputChange = this.handleInputChange.bind(this);
  }

  async componentWillMount() {
    const loadBlock = await loadBlockchainData(this.props.dispatch);
    await this.setState(loadBlock);

    this._ScreenCardManager();
  }


  handleInputChange(event) {
    const target = event.target;
    const value = target.value;
    const name = target.name;

    console.log(target);
    console.log(value);
    console.log(name);

    this.setState({
      [name]: value
    });
  }


  handleInputChangePcoin(value) {
    this.setState({ pCoinEx: value });
  }

  async _ScreenCardManager(){
    let content = [];

    const totalCard = await this.state.CardABI.methods.totalCard().call({from: this.state.account});
    
      for (let i = 1; i <= totalCard; i++) {
        let itemsDetail = await this.state.CardABI.methods._cardAttr( i ).call({from: this.state.account});
        let cardImg = [];
        const rewardRate = this.state.web3.utils.fromWei(itemsDetail.rewardRate);
        if (itemsDetail.mecType === "0") {
          cardImg.push(<Image src={ "https://cryplotto.finance/nft/images/mineCard/"+itemsDetail.rarity+".png" }  style={{height:"250px"}}/>);
        }else{
          cardImg.push(<Image src={ "https://cryplotto.finance/nft/images/mineCard/w1.png" }  style={{height:"250px"}}/>);
        }

        content.push(<>
            <Col sm={6} className="pT10 mT10">
              <Row className="relative">
                <Col className="text-center">
                  {cardImg}
                </Col>
                <Col>
                  <div>
                    <span>Rarity: </span><br/>
                    <input name={"rarity_"+i} type="number" onChange={this.handleInputChange} defaultValue={itemsDetail.rarity} />
                  </div>
                  <div>
                    <span>Reward: </span><br/>
                    <input name={"rewardRate"+i} type="number" onChange={this.handleInputChange} defaultValue={rewardRate} />
                  </div>
                  <div>
                    <span>Energy Consumed: </span><br/>
                    <input name={"energyCon"+i} type="number" onChange={this.handleInputChange} defaultValue={itemsDetail.energyCon} />
                  </div>
                  <div>
                    <span>Durability Consumed: </span><br/>
                    <input name={"durabilityCon"+i} type="number" onChange={this.handleInputChange} defaultValue={itemsDetail.durabilityCon} />
                  </div>
                  <div>
                    <span> Max Durability:</span><br/>
                    <input name={"durability"+i} type="number" onChange={this.handleInputChange} defaultValue={itemsDetail.durability} />
                  </div>

                  <div className="f18 pT10">
                    <button className="button-71" onClick={(e) => this.update( i )}>Update</button>
                  </div>
                  
                </Col>
              </Row>
            </Col>
            </>);
        
      }
    
    
    //
    await this.setState({content:content});
  }

  
  
  

//
  render() {
    return (<>
      
      <Container fluid>
        <Row className="mt-5">
          <Col><span className="f50 labelPage">Admin Control</span></Col>
        </Row>
        <Row xs="auto" className="pT10">
          <Col className="subLink"><Link onClick={(e) => this._ScreenCardManager()} >Cards Manager</Link></Col>
          <Col className="subLink"><Link onClick={(e) => this.loadWithdrawScreen()} >Add New Card</Link></Col>
          <Col className="subLink"><Link onClick={(e) => this.loadDepositScreen()} >Exchange TAX</Link></Col>
          <Col className="subLink"><Link onClick={(e) => this.loadDepositScreen()} >Market Fee</Link></Col>
        </Row>
        <Row className="pT10 mb-50">
          {this.state.content}
        </Row>
      </Container>
    </>);
  }
}

export default MyExchange;
