import { Container, Row, Col, Image } from 'react-bootstrap';
import React, { Component } from 'react';
import Navbar from './Navbar';
import { Link } from "react-router-dom";
import Countdown, {zeroPad} from 'react-countdown';
import { loadBlockchainData, loadInGameCoin, loadWeb3 } from './loadweb3.js';
import no from './images/no.png';
import pCard1 from './images/powerCard/pCard1.png';
import pCard2 from './images/powerCard/pCard2.png';
import pCard3 from './images/powerCard/pCard3.png';

import mCard1 from './images/mineCard/1.png';
import mCard2 from './images/mineCard/2.png';
import mCard3 from './images/mineCard/3.png';

class Market extends Component {

  async componentWillMount() {
    const loadBlock = await loadBlockchainData(this.props.dispatch);
    await this.setState(loadBlock);
    if(typeof loadBlock.account !=='undefined'){
      const coinToken = await loadInGameCoin();
      this.setState(coinToken);

      await this.loadAllCard();
    }
  }

  async connectWallet(){
    await loadWeb3();
    const loadBlock = await loadBlockchainData(this.props.dispatch);
    await this.setState(loadBlock);
  }

  async reloadCoinToken(e){
    const coinToken = await loadInGameCoin();
    this.setState(coinToken);
  }

  async buyItem(marketItemId, price){
    let _amount = price;
    const receipt = await this.state.MarketABI.methods.marketBuy(this.state.CardAddress,marketItemId).send({value: _amount.toString(),from: this.state.account});
    console.log(receipt);
    this.loadAllCard();
  }

  async restoreItem(marketItemId){
    const receipt = await this.state.MarketABI.methods.restoreItem(this.state.CardAddress,marketItemId).send({from: this.state.account});
    console.log(receipt);
    this.loadAllCard();
  }

  async myHistory(e){
    let myItems = [];
    let history = await this.state.MarketABI.methods.history( this.state.account ).call();
    console.log(history);
    if (history.length > 0) {
      for (let i = history.length-1; i >= 0; i--) {

          let itemsDetail = await this.state.CardABI.methods._cardAttr( history[i].cardId ).call({from: this.state.account});
////
          let CardImg;
          if (itemsDetail.mecType === "0") {
            if (itemsDetail.rarity === "1") {
              CardImg = mCard1;
            }else if (itemsDetail.rarity === "2") {
              CardImg = mCard2;
            }else if (itemsDetail.rarity === "3") {
              CardImg = mCard3;
            }
          }else{
            if (itemsDetail.rarity === "1") {
              CardImg = pCard1;
            }else if (itemsDetail.rarity === "2") {
              CardImg = pCard2;
            }else if (itemsDetail.rarity === "3") {
              CardImg = pCard3;
            }
          }
          
///
          let actionDetail = [];
          if(history[i].buyer === "0x0000000000000000000000000000000000000000" && history[i].seller === this.state.account){
            actionDetail.push(<><span>You start to sell card #{history[i].tokenId}</span><br/><span>Price : {this.state.web3.utils.fromWei(history[i].price)}</span></>);
          }
///
          if(history[i].buyer === history[i].seller){
            actionDetail.push(<span>You Restore card #{history[i].tokenId}</span>);
          }
///
          if(history[i].buyer !== history[i].seller && history[i].sold === true ){
            actionDetail.push(<><span>{history[i].buyer.slice(0, 8)} bought Your card #{history[i].tokenId}</span><span>Price : {this.state.web3.utils.fromWei(history[i].price)}</span></>);
          }

          ///
          myItems.push(<>
                <Row className="relative">
                  <Col sm="2">
                    <Image src={ CardImg }  style={{height:"150px"}}/>
                  </Col>
                  <Col>
                    {/*history[i].timeStamp*/}<br/>
                    {actionDetail}
                    
                  </Col>
                </Row>
              </>);
          
        
      }
    }
    await this.setState({myItems:myItems});
  }
///
  async marketMyItems(){
    let myItems = [];
    let listOnMarket = await this.state.MarketABI.methods.marketMyItems( this.state.account ).call();
    console.log(listOnMarket);
    if (listOnMarket.length > 0) {
      for (let i = 0; i < listOnMarket.length; i++) {
//ทำโหลด การ์ดเก็บไวก่อนแต่แรกเลย
          let itemsDetail = await this.state.CardABI.methods._cardAttr( listOnMarket[i].cardId ).call({from: this.state.account});
          console.log(itemsDetail);

          let itemsStatus = await this.state.mineABI.methods._mineDetails( listOnMarket[i].tokenId ).call({from: this.state.account});
          //console.log(itemsStatus);

          let buyOrRestore = [];
          if (listOnMarket[i].seller === this.state.account) {
            buyOrRestore.push(<button className="button-71" onClick={(e) => this.restoreItem( listOnMarket[i].marketItemId )}>Restore</button>);
          }
///
   
          let CardImg;
          if (itemsDetail.mecType === "0") {
            if (itemsDetail.rarity === "1") {
              CardImg = mCard1;
            }else if (itemsDetail.rarity === "2") {
              CardImg = mCard2;
            }else if (itemsDetail.rarity === "3") {
              CardImg = mCard3;
            }
          }else{
            if (itemsDetail.rarity === "1") {
              CardImg = pCard1;
            }else if (itemsDetail.rarity === "2") {
              CardImg = pCard2;
            }else if (itemsDetail.rarity === "3") {
              CardImg = pCard3;
            }
          }

            myItems.push(<>
              <Col sm={4} className="charDetail">
                <Row className="text-center relative">
                  <Col className="pT10 pB10 leftStatus mL10 mb10" >
                    <Image src={ CardImg }  style={{height:"250px"}}/>
                      <div className="pT10"><span>Reward: {this.state.web3.utils.fromWei(itemsDetail.rewardRate)}</span></div>
                      <div><span>Energy Consumed: {itemsDetail.energyCon}</span></div>
                      <div><span>Durability Consumed: {itemsDetail.durabilityCon}</span></div>
                      <div className="f24"><b>{itemsStatus.durabilityCur}/{itemsDetail.durability}</b></div>
                      <div className="pT10">
                        {buyOrRestore}
                      </div>
                  </Col>
                </Row>
              </Col>
{/*
              <Col sm={3} className="charDetail frameR">
                <Row className="text-center relative">
                  <Col>
                     <Image src={ CardImg }  style={{height:"200px"}}/>
                    <div className="pT10"><span>Rarity: {itemsDetail.rarity}</span></div>
                    <div><span>Reward: {itemsDetail.rewardRate}</span></div>
                    <div><span>Energy Consumed: {itemsDetail.energyCon}</span></div>
                    <div><span>Durability Consumed: {itemsDetail.durabilityCon}</span></div>
                    <div className="f24"><b>{itemsStatus.durabilityCur}/{itemsDetail.durability}</b></div>
                    <div>
                      {buyOrRestore}
                    </div>
                  </Col>
                </Row>
              </Col>
*/}
              </>);
          
        
      }
    }
    await this.setState({myItems:myItems});
  
  }
//
  async loadAllCard(e){

    let myItems = [];
    let listOnMarket = await this.state.MarketABI.methods.marketItems().call();
    console.log(listOnMarket);
    if (listOnMarket.length > 0) {
      for (let i = 0; i < listOnMarket.length; i++) {
//ทำโหลด การ์ดเก็บไวก่อนแต่แรกเลย
          let itemsDetail = await this.state.CardABI.methods._cardAttr( listOnMarket[i].cardId ).call({from: this.state.account});
          console.log(itemsDetail);
          const cardPrice = this.state.web3.utils.fromWei(listOnMarket[i].price);

          let itemsStatus = await this.state.mineABI.methods._mineDetails( listOnMarket[i].tokenId ).call({from: this.state.account});
          console.log(itemsStatus);

          let buyOrRestore = [];
          if (listOnMarket[i].seller === this.state.account) {
            buyOrRestore.push(<button className="button-71" onClick={(e) => this.restoreItem( listOnMarket[i].marketItemId )}>Restore</button>);
          }else{
            buyOrRestore.push(<button className="button-71" onClick={(e) => this.buyItem( listOnMarket[i].marketItemId, listOnMarket[i].price )}>Buy</button>);
          }
////
          let CardImg;
          if (itemsDetail.mecType === "0") {
            if (itemsDetail.rarity === "1") {
              CardImg = mCard1;
            }else if (itemsDetail.rarity === "2") {
              CardImg = mCard2;
            }else if (itemsDetail.rarity === "3") {
              CardImg = mCard3;
            }
          }else{
            if (itemsDetail.rarity === "1") {
              CardImg = pCard1;
            }else if (itemsDetail.rarity === "2") {
              CardImg = pCard2;
            }else if (itemsDetail.rarity === "3") {
              CardImg = pCard3;
            }
          }
          myItems.push(<>
              <Col sm={4} className="charDetail">
                <Row className="text-center relative">
                  <Col className="pT10 pB10 leftStatus mL10 mb10" >
                    <Image src={ CardImg }  style={{height:"250px"}}/>
                      <div className="pT10"><span>Reward: {this.state.web3.utils.fromWei(itemsDetail.rewardRate)}</span></div>
                      <div><span>Energy Consumed: {itemsDetail.energyCon}</span></div>
                      <div><span>Durability Consumed: {itemsDetail.durabilityCon}</span></div>
                      <div className="f24"><b>{itemsStatus.durabilityCur}/{itemsDetail.durability}</b></div>
                      <div className="pT10">
                        {buyOrRestore}
                      </div>
                  </Col>
                </Row>
              </Col>
              </>);
          
        
      }
    }
    ///
    await this.setState({myItems:myItems});
  }

//
  
  constructor(props) {
    super(props)
    this.state = {
      web3: 'undefined',
      account: '',
      mineABI: null,
      myItems:[],
    }
    this.reloadCoinToken = this.reloadCoinToken.bind(this);
  }

//
  render() {
    return (<>
      
      { this.state.showModalContent }

      <Navbar isConnect={this.state.isConnect} loadWeb3={this.loadWeb3} connectWallet={this.connectWallet} account={this.state.account} CurrencyABI={this.state.CurrencyABI} mCoin={this.state.mCoin} pCoin={this.state.pCoin} energy={this.state.energy} mineABI={this.state.mineABI} reloadCoinToken={this.reloadCoinToken}/>

      <Container fluid>
        <Row className="mt-5">
          <Col sm={3} className="mL10">
            {this.state.subMenu}

            <Row className="pT10">
              <Col sm={12} className="leftStatus" style={{maxHeight:"340px"}} >
                <Row className="pB10">
                  <Col><Image src={ no } width="12px"/></Col>
                  <Col className="text-right"><Image src={ no } width="12px"/></Col>
                </Row>

                <Row className="text-center">
                  <Col><span className="f32 labelPage">MARKET</span></Col>
                </Row>

                <Row className="mT10 pT10 pB5 subLinkTab">
                  <Col>
                    <Link onClick={(e) => this.loadAllCard()}>All Market</Link>
                  </Col>
                </Row>

                <Row className="mT10 pT10 pB5 subLinkTab">
                  <Col onClick={(e) => this.marketMyItems()}><Link>My Items</Link></Col>
                </Row>

                <Row className="mT10 pT10 pB5 subLinkTab">
                  <Col onClick={(e) => this.myHistory()}><Link>My History</Link></Col>
                </Row>

                <Row className="pT10">
                  <Col><Image src={ no } width="12px"/></Col>
                  <Col className="text-right"><Image src={ no } width="12px"/></Col>
                </Row>

              </Col>
            </Row>
          </Col>

           <Col sm={8} className="mL40">
            <Row>
              {this.state.myItems}
            </Row>
          </Col>

        </Row>

{/*
        <Row className="mt-5">
          <Col><span className="f50 labelPage">Market</span></Col>
        </Row>
        <Row className="mt-2" >
          <Col sm="3">
            <div onClick={(e) => this.loadAllCard()}><Link>All Market Items</Link></div>
            <div onClick={(e) => this.marketMyItems()}><Link>My Items</Link></div>
            <div onClick={(e) => this.myHistory()}><Link>My History</Link></div>
          </Col>
          <Col sm="9">
            <Row>
              {this.state.myItems}
            </Row>
          </Col>
        </Row>
*/}
      </Container>
    </>);
  }
}

export default Market;
