import { Container, Row, Col, Image, Button } from 'react-bootstrap';
import React, { Component } from 'react';
import Navbar from './Navbar';
import { Link } from "react-router-dom";
import { loadBlockchainData, loadInGameCoin, loadWeb3 } from './loadweb3.js';
import no from './images/no.png';

class MyExchange extends Component {

  async componentWillMount() {
    const loadBlock = await loadBlockchainData(this.props.dispatch);
    await this.setState(loadBlock);
    if(typeof loadBlock.account !=='undefined'){
      const coinToken = await loadInGameCoin();
      this.setState(coinToken);

      await this.loadWithdrawScreen();
    }
  }

  async connectWallet(){
    await loadWeb3();
    const loadBlock = await loadBlockchainData(this.props.dispatch);
    await this.setState(loadBlock);
  }

  async reloadCoinToken(e){
    const coinToken = await loadInGameCoin();
    this.setState(coinToken);
  }

  handleInputChangeMcoin(value) {
    this.setState({ mCoinEx: value });
  }

  handleInputChangePcoin(value) {
    this.setState({ pCoinEx: value });
  }

  async Withdraw(e){
    
    console.log("withdraw");
    console.log(this.state.mCoin);
    console.log(this.state.pCoin);
    console.log(this.state.mCoinEx);
    console.log(this.state.pCoinEx);

    if ( parseInt( this.state.mCoinEx ) > parseInt( this.state.mCoin ) ) {
      alert("Money not enough!!!");
      return;
    }

    if ( parseInt( this.state.pCoinEx ) > parseInt( this.state.pCoin ) ) {
      alert("Power not enough!!!");
      return;
    }

    const mCoinDe = this.state.web3.utils.toWei( this.state.mCoinEx.toString() );
    const pCoinDe = this.state.web3.utils.toWei( this.state.pCoinEx.toString() );

    console.log( mCoinDe );

    let withdrawDetail = await this.state.CurrencyABI.methods.withdraw( mCoinDe.toString(), pCoinDe.toString() ).send({from: this.state.account});
    console.log(withdrawDetail);

    this.reloadCoinToken();
  }

  async Deposit(e){
    const mCoinDe = this.state.web3.utils.toWei( this.state.mCoinEx.toString() );
    const pCoinDe = this.state.web3.utils.toWei( this.state.pCoinEx.toString() );
    let depositDetail = await this.state.CurrencyABI.methods.deposit( mCoinDe.toString(), pCoinDe.toString() ).send({from: this.state.account});
    console.log(depositDetail);
    this.reloadCoinToken();
  }

  async approveToken(e) {
    await this.state.MMCTokenABI.methods.approve(this.state.CurrencyAddress, '0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff').send({from: this.state.account})
    await this.state.ENCTokenABI  .methods.approve(this.state.CurrencyAddress, '0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff').send({from: this.state.account})
  }

  async loadWithdrawScreen(e){
    let subMenu = [];
    subMenu.push(<Row >
              <Col sm={12} className="leftStatus">
                <Row>
                  <Col className="subLinkTab text-center f20">
                    <div className="button01 mA4">
                      <Link onClick={(e) => this.loadWithdrawScreen()}>Withdraw </Link>
                    </div>
                  </Col>
                  <Col className="subLinkTab text-center f20">
                    <div className="mA4">
                      <Link onClick={(e) => this.loadDepositScreen()}>Deposit </Link>
                    </div>
                  </Col>
                </Row>
              </Col>
            </Row>);
    await this.setState({subMenu:subMenu});

    let myItems = [];
    myItems.push(<>
      <Row className="mT10 pT10" >
        <Col className="f24 text-center"><span>Withdraw Fee: {this.state.fee}%</span></Col>
      </Row>

      <Row xs="auto" className="pT10 space-center">
        <Col className="text-center">
          <span>Money</span><br/>
          <input type="number" id="quantity" name="quantity" min="1" max="20" style={{width:"200px"}} onChange={ e => this.handleInputChangeMcoin(e.target.value) } />
        </Col>
        <Col className="text-center">
          <span>Power</span><br/>
          <input type="number" id="quantity" name="quantity" min="1" max="20" style={{width:"200px"}} onChange={ e => this.handleInputChangePcoin(e.target.value) } />
        </Col>
      </Row>

      <Row xs="auto" className="pT10 space-center">
        <Col sm="6" className="text-center">
          <button className="button-71 buttonMining" onClick={(e) => this.Withdraw()}>Withdraw</button>
        </Col>
      </Row>
      </>);

    await this.setState({myItems:myItems});
  }

  async loadDepositScreen(e){
    let subMenu = [];
    subMenu.push(<Row >
              <Col sm={12} className="leftStatus">
                <Row>
                  <Col className="subLinkTab text-center f20">
                    <div className="mA4">
                      <Link onClick={(e) => this.loadWithdrawScreen()}>Withdraw </Link>
                    </div>
                  </Col>
                  <Col className="subLinkTab text-center f20">
                    <div className="button01 mA4">
                      <Link onClick={(e) => this.loadDepositScreen()}>Deposit </Link>
                    </div>
                  </Col>
                </Row>
              </Col>
            </Row>);
    await this.setState({subMenu:subMenu});

    let myItems = [];
    myItems.push(<>
      <Row className="mT10 pT10" >
        <Col className="f24 text-center"><span>Deposit</span></Col>
      </Row>

      <Row xs="auto" className="pT10 space-center">

        <Col className="text-center">
          <span>Money</span><br/>
          <input type="number" id="quantity" name="quantity" min="1" max="20" style={{width:"200px"}} onChange={ e => this.handleInputChangeMcoin(e.target.value) } />
        </Col>

        <Col className="text-center">
          <span>Power</span><br/>
          <input type="number" id="quantity" name="quantity" min="1" max="20" style={{width:"200px"}} onChange={ e => this.handleInputChangePcoin(e.target.value) } />
        </Col>

      </Row>

      <Row xs="auto" className="pT10 space-center">
        <Col sm="6" className="text-center">
          <button className="button-71 buttonMining" onClick={(e) => this.approveToken()}>Approve</button>
          <button className="button-71 buttonMining mL10" onClick={(e) => this.Deposit()}>Deposit</button>
        </Col>
      </Row>
      </>);

    await this.setState({myItems:myItems});
  }

  
  constructor(props) {
    super(props)
    this.state = {
      web3: 'undefined',
      account: '',
      mineABI: null,
      myItems:[],
      mCoinEx:0,
      pCoinEx:0,
      mCoin:0,
      pCoin:0,
      fee:0,
    }
    this.reloadCoinToken = this.reloadCoinToken.bind(this);
  }

//
  render() {
    return (<>
      
      { this.state.showModalContent }

      <Navbar isConnect={this.state.isConnect} loadWeb3={this.loadWeb3} connectWallet={this.connectWallet} account={this.state.account} CurrencyABI={this.state.CurrencyABI} mCoin={this.state.mCoin} pCoin={this.state.pCoin} energy={this.state.energy} mineABI={this.state.mineABI} reloadCoinToken={this.reloadCoinToken}/>

      <Container fluid>
        <Row className="mt-5">
          <Col sm={3} className="mL10">
            {this.state.subMenu}

            <Row className="pT10">
              <Col sm={12} className="leftStatus" style={{maxHeight:"220px"}} >
                <Row className="pB10">
                  <Col><Image src={ no } width="12px"/></Col>
                  <Col className="text-right"><Image src={ no } width="12px"/></Col>
                </Row>

                <Row className="text-center">
                  <Col><span className="f32 labelPage">EXCHANGE</span></Col>
                </Row>

                <Row className="pT10">
                  <Col><Image src={ no } width="12px"/></Col>
                  <Col className="text-right"><Image src={ no } width="12px"/></Col>
                </Row>

              </Col>
            </Row>
          </Col>

          <Col sm={8} className="mL40">
            <Row>
              {this.state.myItems}
            </Row>
          </Col>

        </Row>
      </Container>
    </>);
  }
}

export default MyExchange;
