import { Container, Row, Col, Image, Button } from 'react-bootstrap';
import React, { Component } from 'react';
import Web3 from 'web3';
import Navbar from './Navbar';
import { Link } from "react-router-dom";
import Countdown, {zeroPad} from 'react-countdown';
import { loadBlockchainData, loadInGameCoin, loadWeb3 } from './loadweb3.js';
import no from './images/no.png';
import pCard1 from './images/powerCard/pCard1.png';
import pCard2 from './images/powerCard/pCard2.png';
import pCard3 from './images/powerCard/pCard3.png';

import mCard1 from './images/mineCard/1.png';
import mCard2 from './images/mineCard/2.png';
import mCard3 from './images/mineCard/3.png';

class MyInventroy extends Component {

  async componentWillMount() {
    const loadBlock = await loadBlockchainData(this.props.dispatch);
    await this.setState(loadBlock);
    if(typeof loadBlock.account !=='undefined'){
      const coinToken = await loadInGameCoin();
      this.setState(coinToken);

      await this.loadMachine();
    }
  }

  async connectWallet(){
    await loadWeb3();
    const loadBlock = await loadBlockchainData(this.props.dispatch);
    await this.setState(loadBlock);
  }

  async reloadCoinToken(e){
    const coinToken = await loadInGameCoin();
    this.setState(coinToken);
  }

  async installCard(indexId){
    console.log( indexId );
    const installIndex = await this.state.mineABI.methods.stakingCard( indexId ).send({from: this.state.account});
    this.loadMachine();
  }

  showModal = () => {
    this.setState({ showModal: true });
  };

  hideModal = () => {
    this.setState({ showModal: false, showModalContent : [] });
  };

  handleInputChange(value) {
    console.log(value)
    this.setState({ price: value });
  }

  async sellCardModal(indexId, itemsDetail, itemsStatus){
    let Content = [];
    const cardId = itemsStatus.cardId;
    const isApprovedForMarket = await this.state.CardABI.methods.isApprovedForAll(this.state.account,this.state.MarketAddress).call({from: this.state.account});
    //console.log(isApprovedForMarket);
   

    let checkApprove = [];
    if (isApprovedForMarket) {
        checkApprove.push( <Button className="button-71 buttonMining" onClick={(e) => this.sellCard(indexId,cardId)}>Sell</Button> );
    }else{
        checkApprove.push( <Button className="button-71 buttonMining" onClick={(e) => this.approvedAllCard(indexId, itemsDetail, itemsStatus)}>Approve</Button> );          
    }

    await this.setState({isApprovedForMarket:isApprovedForMarket, checkApprove:checkApprove});

    let CardImg;
    if (itemsDetail.mecType === "0") {
      if (itemsDetail.rarity === "1") {
        CardImg = mCard1;
      }else if (itemsDetail.rarity === "2") {
        CardImg = mCard2;
      }else if (itemsDetail.rarity === "3") {
        CardImg = mCard3;
      }
    }else{
      if (itemsDetail.rarity === "1") {
        CardImg = pCard1;
      }else if (itemsDetail.rarity === "2") {
        CardImg = pCard2;
      }else if (itemsDetail.rarity === "3") {
        CardImg = pCard3;
      }
    }

    Content.push(<>
        <div className="Modal" /*onClick={(e) => this.hideModal(e)}*/>
            <div className="adventureModal pA20 white" >

              <Row className="ab_Top">
                <Col><Image src={ no } width="12px"/></Col>
                <Col className="text-right"><Image src={ no } width="12px"/></Col>
              </Row>

              <Row className="mb10">
                <Col className="text-center close f24">
                  <a href="#" onClick={(e) => this.hideModal(e)} >X CLOSE</a>
                </Col>
              </Row>

              <Row className="mT10 space-center pT10">
                <Col className="text-center" sm="3">
                  <Image src={ CardImg }  style={{height:"250px"}}/>
                </Col>
                <Col sm="auto">
                  <div className="pT10"><span>Rarity: {itemsDetail.rarity}</span></div>
                  <div><span>Reward: {this.state.web3.utils.fromWei(itemsDetail.rewardRate)}</span></div>
                  <div><span>Energy Consumed: {itemsDetail.energyCon}</span></div>
                  <div><span>Durability Consumed: {itemsDetail.durabilityCon}</span></div>
                  <div className="f24"><b>{itemsStatus.durabilityCur}/{itemsDetail.durability}</b></div>
                    <div>
                      <label for="quantity">Price :</label>
                      <input type="number" id="quantity" name="quantity" min="1" max="20" defaultValue="0" style={{width:"100px"}} onChange={ e => this.handleInputChange(e.target.value) } />
                    </div>
                    <div className="mT10">
                    { this.state.checkApprove }
                    </div>
                </Col>
              </Row>

              <Row className="pT10 ab_bottom">
                  <Col><Image src={ no } width="12px"/></Col>
                  <Col className="text-right"><Image src={ no } width="12px"/></Col>
                </Row>
            </div >
        </div >
    </>);
    this.setState({showModalContent:Content});
///
    
    //let listOnMarket = await this.state.marketABI.methods.createMarketItem(MineContact.networks[this.state.netId].address, indexId, price.toString()).send({from: this.state.account});
    //console.log(listOnMarket);
  }

  async sellCard(indexId,cardId){
    let price = this.state.price * 10**18;
    const installIndex = await this.state.CardABI.methods.sellCard(indexId, price.toString(), cardId ).send({from: this.state.account});
    console.log(installIndex);
    this.hideModal();
    this.loadMachine();

    
    //let listOnMarket = await this.state.marketABI.methods.createMarketItem(MineContact.networks[this.state.netId].address, indexId, price.toString()).send({from: this.state.account});
    //console.log(listOnMarket);
  } 

  async approvedAllCard(indexId, itemsDetail, itemsStatus){
    const installIndex = await this.state.CardABI.methods.setApprovedMarket().send({from: this.state.account});
    //console.log(installIndex);
    this.sellCardModal(indexId, itemsDetail, itemsStatus);
  }

  async loadMachine(e){
    let subMenu = [];
    subMenu.push(<Row >
              <Col sm={12} className="leftStatus">
                <Row>
                  <Col className="subLinkTab text-center f20">
                    <div className="button01 mA4">
                      <Link onClick={(e) => this.loadMachine()}>Mining </Link>
                    </div>
                  </Col>
                  <Col className="subLinkTab text-center f20">
                    <div className="mA4">
                      <Link onClick={(e) => this.loadResource()}>Resource </Link>
                    </div>
                    {/*<Link to="/resource">Resource Machine</Link>*/}
                  </Col>
                </Row>
              </Col>
            </Row>);
    await this.setState({subMenu:subMenu});

    let myItems = [];
    console.log(this.state.mineABI);
    const itemsIndex = await this.state.mineABI.methods.getAllMineCard( this.state.account ).call({from: this.state.account});

    //console.log(itemsIndex);
    let machineCards = 0;
    let resourceCards = 0;
    if (itemsIndex.length > 0) {

      for (let i = 0; i < itemsIndex.length; i++) {
        //Call แบบไม่ต้องไปที่ฟังชั่น
        let itemsStatus = await this.state.mineABI.methods._mineDetails( itemsIndex[i] ).call({from: this.state.account});
        //console.log(itemsStatus);
        if (itemsStatus.status === "0") {
          let itemsDetail = await this.state.CardABI.methods._cardAttr( itemsStatus.cardId ).call({from: this.state.account});
          let mineCard;
          if (itemsDetail.rarity === "1") {
            mineCard = mCard1;
          }else if (itemsDetail.rarity === "2") {
            mineCard = mCard2;
          }else if (itemsDetail.rarity === "3") {
            mineCard = mCard3;
          }
          //console.log(itemsDetail);
          if (itemsDetail.mecType === "1") {
            resourceCards += 1;
          }
          if (itemsDetail.mecType === "0") {
            machineCards += 1;
            myItems.push(<>
                <Col sm={4} className="charDetail ">
                  <Row className="text-center relative">
                    <Col className="pT10 pB10 leftStatus mL10 mb10" >
                      <Image src={ mineCard }  style={{height:"250px"}}/>
                      {/*<div className="pT10"><span>Rarity: {itemsDetail.rarity}</span></div>*/}
                      <div><span>Reward: {this.state.web3.utils.fromWei(itemsDetail.rewardRate)}</span></div>
                      <div><span>Energy Consumed: {itemsDetail.energyCon}</span></div>
                      <div><span>Durability Consumed: {itemsDetail.durabilityCon}</span></div>
                      <div className="f24"><b>{itemsStatus.durabilityCur}/{itemsDetail.durability}</b></div>
                      <div className="pT10">
                        <button className="button-71 buttonMining" onClick={(e) => this.installCard( itemsIndex[i] )}>Install</button>
                        <button className="button-71 buttonMining mL5" onClick={(e) => this.sellCardModal( itemsIndex[i], itemsDetail, itemsStatus )}>Sell</button>
                      </div>
                    </Col>
                  </Row>
                </Col>
                </>);
          }
        }
      }
    }

    
    //
    await this.setState({myItems:myItems,resourceCards:resourceCards,machineCards:machineCards});
  }

  async loadResource(e){
    let subMenu = [];
    subMenu.push(<Row >
              <Col sm={12} className="leftStatus">
                <Row>
                  <Col className="subLinkTab text-center f20">
                    <div className="mA4">
                      <Link onClick={(e) => this.loadMachine()}>Mining </Link>
                    </div>
                  </Col>
                  <Col className="subLinkTab text-center f20">
                    <div className="button01 mA4">
                      <Link onClick={(e) => this.loadResource()}>Resource </Link>
                    </div>
                    {/*<Link to="/resource">Resource Machine</Link>*/}
                  </Col>
                </Row>
              </Col>
            </Row>);
    await this.setState({subMenu:subMenu});
    let myItems = [];
    const itemsIndex = await this.state.mineABI.methods.getAllMineCard( this.state.account ).call({from: this.state.account});
    //console.log(itemsIndex);
    let machineCards = 0;
    let resourceCards = 0;
    if (itemsIndex.length > 0) {

      for (let i = 0; i < itemsIndex.length; i++) {
        //Call แบบไม่ต้องไปที่ฟังชั่น
        let itemsStatus = await this.state.mineABI.methods._mineDetails( itemsIndex[i] ).call({from: this.state.account});
        //console.log(itemsStatus);
        if (itemsStatus.status === "0") {
          let itemsDetail = await this.state.CardABI.methods._cardAttr( itemsStatus.cardId ).call({from: this.state.account});
          let powerCard;
          if (itemsDetail.rarity === "1") {
            powerCard = pCard1;
          }else if (itemsDetail.rarity === "2") {
            powerCard = pCard2;
          }else if (itemsDetail.rarity === "3") {
            powerCard = pCard3;
          }
          //console.log(itemsDetail);
          if (itemsDetail.mecType === "0") {
            machineCards += 1;
          }
          if (itemsDetail.mecType === "1") {
            resourceCards += 1;
            myItems.push(<>
                <Col sm={4} className="charDetail">
                  <Row className="text-center relative">
                    <Col className="pT10 pB10 leftStatus mL10 mb10" >
                      <Image src={ powerCard }  style={{height:"250px"}}/>
                        <div><span>Reward: {this.state.web3.utils.fromWei(itemsDetail.rewardRate)}</span></div>
                        <div><span>Energy Consumed: {itemsDetail.energyCon}</span></div>
                        <div><span>Durability Consumed: {itemsDetail.durabilityCon}</span></div>
                        <div className="f24"><b>{itemsStatus.durabilityCur}/{itemsDetail.durability}</b></div>
                        <div className="pT10">
                          <button className="button-71 buttonMining" onClick={(e) => this.installCard( itemsIndex[i] )}>Install</button>
                          <button className="button-71 buttonMining mL5" onClick={(e) => this.sellCardModal( itemsIndex[i], itemsDetail, itemsStatus )}>Sell</button>
                        </div>
                    </Col>
                  </Row>
                </Col>
                </>);
          }
        }
      }
    }
    
    //
    await this.setState({myItems:myItems,resourceCards:resourceCards,machineCards:machineCards,subMenu:subMenu});
  }

  
  constructor(props) {
    super(props)
    this.state = {
      web3: 'undefined',
      account: '',
      mineABI: null,
      myItems:[],
      price:0,
      machineCards:0,
      resourceCards:0,
    }
    this.reloadCoinToken = this.reloadCoinToken.bind(this);
  }

//
  render() {
    return (<>
      
      { this.state.showModalContent }

      <Navbar isConnect={this.state.isConnect} connectWallet={this.connectWallet} account={this.state.account} CurrencyABI={this.state.CurrencyABI} mCoin={this.state.mCoin} pCoin={this.state.pCoin} energy={this.state.energy} mineABI={this.state.mineABI} reloadCoinToken={this.reloadCoinToken}/>

      <Container fluid>
        <Row className="mt-5">
          <Col sm={3} className="mL10">
            {this.state.subMenu}

            <Row className="pT10">
              <Col sm={12} className="leftStatus" style={{maxHeight:"220px"}} >
                <Row className="pB10">
                  <Col><Image src={ no } width="12px"/></Col>
                  <Col className="text-right"><Image src={ no } width="12px"/></Col>
                </Row>

                <Row className="text-center">
                  <Col><span className="f32 labelPage">INVENTORY</span></Col>
                </Row>

                <Row className="mT10 text-center fadeBg pT10 pB5">
                  <Col>
                    <span className="f18 labelPage">Machine Cards</span>
                  </Col>
                  <Col>
                    <span className="orange">{this.state.machineCards}</span>
                  </Col>
                    {/*<button className="button-71"  onClick={(e) => this.upgrade()}>Upgrade</button>*/}
                </Row>
                <Row className="text-center fadeBg pT10 pB5">
                  <Col>
                    <span className="f18 labelPage">Resource Cards </span>
                  </Col>
                  <Col>
                    <span className="orange">{this.state.resourceCards}</span>
                    {/*<button className="button-71"  onClick={(e) => this.upgrade()}>Upgrade</button>*/}
                  </Col>
                </Row>
                <Row className="pT10">
                  <Col><Image src={ no } width="12px"/></Col>
                  <Col className="text-right"><Image src={ no } width="12px"/></Col>
                </Row>

              </Col>
            </Row>
          </Col>

          <Col sm={8} className="mL40">
            <Row>
              {this.state.myItems}
            </Row>
          </Col>

        </Row>

      </Container>
    </>);
  }
}

export default MyInventroy;
