import { Container, Row, Col, Image } from 'react-bootstrap';
import React, { Component } from 'react';
import Web3 from 'web3';
import MineContact from './abis/Mine.json';
import MarketContact from './abis/Market.json';
import Navbar from './Navbar';
import { Link } from "react-router-dom";
import Countdown, {zeroPad} from 'react-countdown';

class History extends Component {

  async componentWillMount() {
    await this.loadBlockchainData(this.props.dispatch);
    await this.loadAllCard();
    await this.loadInGameCoin();
  }

  async loadWeb3(dispatch) {
    if (window.ethereum) {
      window.ethereum.request({
        method: "wallet_addEthereumChain",
        params: [{chainId: "0x61",
          chainName: "BSCTESTNET",
          rpcUrls: ["https://data-seed-prebsc-1-s1.binance.org:8545"],
          nativeCurrency: {
            name: "BINANCE COIN",
            symbol: "BNB",
            decimals: 18,
          },
          blockExplorerUrls: ["https://testnet.bscscan.com/"],
        }],
      });

      window.web3 = new Web3(window.ethereum)
      await window.ethereum.enable()
    }
    else if (window.web3) {
      window.web3 = new Web3(window.web3.currentProvider)
      console.log('currentProvider');
    }
    else {
      window.alert('Non-Ethereum browser detected. You should consider trying MetaMask!')
    }
  }

  async loadBlockchainData(dispatch) {
    if(typeof window.ethereum!=='undefined'){
      window.ethereum.on("accountsChanged", (accounts: string[]) => {
        window.location.reload();
      });
      // Subscribe to chainId change
      window.ethereum.on("chainChanged", (chainId: number) => {
        window.location.reload();
      });

      // Subscribe to provider disconnection
      window.ethereum.on("disconnect", (error: { code: number; message: string }) => {
        console.log(error);
      });

      const web3 = new Web3(window.ethereum);
      const netId = await web3.eth.net.getId();
      const accounts = await web3.eth.getAccounts();

      const mineContactAddress = MineContact.networks[netId].address;
      //console.log(mineContactAddress);
      await this.setState({mineContactAddress:mineContactAddress});

      //load user account balance
      if(typeof accounts[0] !=='undefined'){
        const balance = await web3.eth.getBalance(accounts[0]);
        const accountBalance =  web3.utils.fromWei(balance);
        const isConnect = true;
        //console.log(accounts[0]);
        await this.setState({account: accounts[0], balance: balance, web3: web3, accountBalance:accountBalance, isConnect:isConnect});
      } else {
        //window.alert('Please login with MetaMask')
      }

      //load contracts
      try {
        const mineABI = new web3.eth.Contract(MineContact.abi, MineContact.networks[netId].address);
        const marketABI = new web3.eth.Contract(MarketContact.abi, MarketContact.networks[netId].address);
        this.setState({loading: true, mineABI: mineABI, marketABI:marketABI});

      } catch (e) {
        console.log('Error', e);
        //window.alert('Contracts not deployed to the current network')
      }
    } 
  }

  async loadInGameCoin(e){
    let coinDetail = await this.state.mineABI.methods._coin( this.state.account ).call({from: this.state.account});
    const mCoin = coinDetail.mCoin;
    const pCoin = coinDetail.pCoin;
    const energy = coinDetail.energy;
    //console.log(coinDetail);
    await this.setState({mCoin: mCoin, pCoin: pCoin,energy:energy});
  }

  async loadAllCard(e){
    let myItems = [];
    let history = await this.state.marketABI.methods.history( this.state.account ).call();
    console.log(history);
    if (history.length > 0) {
      for (let i = history.length-1; i >= 0; i--) {

          let itemsDetail = await this.state.mineABI.methods._cardAttr( history[i].cardId ).call({from: this.state.account});
////
          let cardImg = [];
          if (itemsDetail.mecType === "0") {
            cardImg.push(<Image src={ "https://cryplotto.finance/nft/images/mineCard/"+itemsDetail.rarity+".png" }  style={{height:"150px"}}/>);
          }else{
            cardImg.push(<Image src={ "https://cryplotto.finance/nft/images/mineCard/w1.png" }  style={{height:"150px"}}/>);
          }
///
          let actionDetail = [];
          if(history[i].buyer === "0x0000000000000000000000000000000000000000" && history[i].seller === this.state.account){
            actionDetail.push(<><span>You start to sell card #{history[i].tokenId}</span><br/><span>Price : {this.state.web3.utils.fromWei(history[i].price)}</span></>);
          }
///
          if(history[i].buyer === history[i].seller){
            actionDetail.push(<span>You Restore card #{history[i].tokenId}</span>);
          }
///
          if(history[i].buyer !== history[i].seller && history[i].sold === true ){
            actionDetail.push(<><span>{history[i].buyer.slice(0, 8)} bought Your card #{history[i].tokenId}</span><span>Price : {this.state.web3.utils.fromWei(history[i].price)}</span></>);
          }

          ///
          myItems.push(<>
                <Row className="relative">
                  <Col sm="2">
                    {cardImg}
                  </Col>
                  <Col>
                    {history[i].timeStamp}<br/>
                    {actionDetail}
                    
                  </Col>
                </Row>
              </>);
          
        
      }
    }
    await this.setState({myItems:myItems});
  }

//
  
  constructor(props) {
    super(props)
    this.state = {
      web3: 'undefined',
      account: '',
      mineABI: null,
      myItems:[],
    }
    this.loadInGameCoin = this.loadInGameCoin.bind(this);
  }

//
  render() {
    return (<>
      
      { this.state.showModalContent }

      <Navbar isConnect={this.state.isConnect} loadWeb3={this.loadWeb3} account={this.state.account} mCoin={this.state.mCoin} pCoin={this.state.pCoin} energy={this.state.energy} mineABI={this.state.mineABI} loadInGameCoin={this.loadInGameCoin}/>

      <Container fluid>
        <Row className="mt-5">
          <Col><span className="f50 labelPage">History</span></Col>
        </Row>
        <Row className="mt-2" >
          <Col sm="3">
            <div>My Items</div>
            <div><Link to="/market">Market</Link></div>
            <div>Filter</div>
          </Col>
          <Col sm="9">
              {this.state.myItems}
          </Col>
        </Row>

      </Container>
    </>);
  }
}

export default History;
